*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth; }

body {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff; }

body main {
  flex: 1 1 auto; }

a {
  color: #000000;
  text-decoration: none; }

ul:not([class]) {
  list-style: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 50px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 25px; } }

.button {
